<template>
  <div class="row max-height-vertical box-overflow">
    <div class="row scroll-outer scroll-padding-bottom">

      <div class="span-14-lg offset-1-lg span-14-md offset-1-md span-14-sm offset-1-sm span-14-xs offset-1-xs bump-up-24-lg">
        <p class="large">{{title}}</p>
      </div>

      <!-- container for selector container and indicator card -->
      <div class="row span-16-lg span-16-md span-16-sm span-16-xs  bump-up-60-lg">
      
        <!-- selectors -->
        <div class="span-9-lg offset-1-lg span-8-md max-height-selector overflow-hidden ">
          <div class="scroll-outer block-important">

              <progress-selector v-for="(card, index) in content.cards" v-bind:key="index" v-bind:index="index" v-bind:activeindex="offset_index" v-on:clicked="selectorCallback(card, index)" v-bind:ref="index + 1">
                {{card.title}}
              </progress-selector>

              <div class="scroll-spacer" :style="spacerHeight"></div>

          </div>
        </div>

        <!-- card -->
        <div class="span-4-lg offset-1-lg span-6-md offset-1-md span-16-sm span-16-xs bg-white border-blue-hover selector-indicator-card content-translate center-flex-self" container-card="selector-card" ref="selectorCard" :style="ratio_styles" v-on:click="toggleIndicator" v-bind:class="[indicatorClass, indicatorVisibleClass, contentActiveClass]">

          <!-- mobile back arrow -->
          <div class="mobile-only arrow-back-container" v-on:click="toggleIndicator">
            <arrow type="prev"></arrow>
          </div>

          <div class="overflow-hidden center-flex cover-class crosshair-rollover-trigger">
            <div class="scroll-outer maintain-outer">
              <div v-if="card" class="card-inner">
                <component v-bind:is="card.icon" class="big-svg"></component>
                <p class="subheader">{{card.title}}</p>
                <p v-html="markedCard.paragraph"></p>
              </div>
            </div>
          </div>

          <crosshair-background></crosshair-background>

        </div>

      </div>
      <!--  -->

    </div>
  </div>
</template>

<script>

  import progress_selector from '../../components/molecules/progress_selector'
  import rollover_card from '../../components/molecules/rollover_card'
  import header_block from '../../components/organisms/header_block'
  import crosshair_background from '../../components/atoms/crosshair_background'
  import arrow from '../../components/atoms/arrow'
  import svg from '../../components/svg'

  import vanilla_smooth_scroll from '../../utils/vanilla_smooth_scroll'
  import {ratio_height, ratio_styles} from '../../utils/ratio_height'
  import {is_active} from '../../utils/is_active'
  import marked from 'marked'

  import 'nodelist-foreach-polyfill'; // ie11


  // TODO: cleartimeouts and set scrolltos

  export default {
    props: ['title', 'content', 'active'],
    data () {
      return {
        card: null,
        offset_index: 0,
        multiplier: 84,
        ratio_styles,
        indicatorClass: 'indicator-not-in-view',
        indicatorVisibleClass: 'indicator-invisible',
        contentActiveClass: 'content-inactive',
        containerRef: null
      }
    },
    methods: {
      createMarked (str) {
        return marked(str)
      },
      set_card (content, index) {
        this.card = content
        this.offset_index += 1
        is_active(this, this.conditional())
      },
      selectorCallback (c, i) {

        this.indicatorVisibleClass = 'indicator-invisible'

        // set card
        setTimeout(() => {
          this.indicatorVisibleClass = ''
          this.set_card(c, i)
        }, 400)

        if(this.indicatorClass !== '') {
          this.toggleIndicator()
        }
      },
      conditional () {
        return this.active && this.offset_index < this.content.cards.length
      },
      toggleIndicator () {
        if (this.indicatorClass === '') {
          this.indicatorClass = 'indicator-not-in-view'
        } else {
          this.indicatorClass = ''
        }
      },
      ratioInit () {
        ratio_height(this, this.$refs.selectorCard, 4/3)
        window.addEventListener('resize', () => {
          setTimeout(() => {
            ratio_height(this, '[container-card="selector-card"]', 4/3)
          }, 500) // prevent large sizes when rezising window from mobile to tablet
        })
      },
      activeCallback () {
        let int = 800
        if(this.active) {
          setTimeout(() => {
            this.ratioInit()
            this.contentActiveClass = ''
          }, int)
        } else {
          setTimeout(() => {
            this.contentActiveClass = 'content-inactive'
          }, int)
        }
      }
    },
    computed: {
      spacerHeight () {
        if(this.containerRef) {
          return this.containerRef.offsetHeight
        }
      },
      transform_style () {
        let offset =  (this.offset_index * this.multiplier) - this.multiplier
        return {
          'transform': 'translateY(-' + offset + 'px)'
        }
      },
      markedCard () {
        if(this.card) {
          return {
            title: marked(this.card.title),
            paragraph: marked(this.card.paragraph)
          }
        }
      }
    },
    components: {
      'progress-selector': progress_selector,
      'rollover-card': rollover_card,
      'header-block': header_block,
      'crosshair-background': crosshair_background,
      'arrow': arrow,
      ...svg
    },
    watch: {
      active () {
        this.activeCallback()
        is_active(this, this.conditional())
      }
    },
  }
</script>

<style scoped lang="scss">
  @import '../../styles/variables';

  .selector-indicator-card {
    position: relative;
    border: 1px solid transparent;
    transition: all $transition-1, transform 1s ease;
    .crosshair-rollover-trigger {
      transition: opacity $transition-1;
    }
    &.indicator-invisible {
      .crosshair-rollover-trigger {
        opacity: 0;
      }  
    }
    @media screen and (max-width: $sm-max) {
      position: fixed;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh !important;
      background: white;

      &.indicator-not-in-view {
        transform: translateX(100%);
        opacity: 0;
      }
    }
  }

  .max-height-selector {
    position: relative;
    min-height: calc(72px * 3);
    // max-height: calc(100vh - 96px - 98px - 48px); // todo: set this with js
    max-height: 100%;
    @media screen and (max-width: $md-max) {
      height: auto;
      max-height: 100% !important;
    }
  }

  .bump-up-60-lg {
    margin-top: -60px;
    @media screen and (max-width: $md-max) {
      margin-top: auto;
    }
  }

  .bump-up-24-lg {
    margin-top: -24px;
    @media screen and (max-width: $md-max) {
      margin-top: auto;
    }
  }

  .card-inner {
    position: relative;
    margin: auto;
    text-align: center;
    padding: 24px;
  }

  .arrow-back-container {
    position: absolute;
    top: 0;
    left: 0;
    height: 24px;
    width: 48px;
    margin-top: 24px;
    margin-left: 24px;
  }

  .progress-selector-container {
    position: relative;
    width: 100%;  
  }

  .block-important {
    display: block !important;
    
  }

  .center-flex-self {
    display: flex;
    align-self: center;

  }

  .scroll-spacer {
    position: relative;
    width: 100%;
    background: blue;
    @media screen and (max-width: $md-max) {
      display: none;
    }
  }

</style>


